import React from 'react';
//import WrappedRoute from './WrappedRoute';
import { Route, Switch, Redirect } from 'react-router-dom';
import Home from './components/Home';
import HowItWorks from './components/HowItWorks';
import Contact from './components/Contact';
import Testimonials from './components/Testimonials';

const AppRoutes = () => {
  const prefix = process.env === "production" ? process.env.PUBLIC_URL : "";
  return (
    <Switch>
      <Route 
        exact
        path={`${prefix}/`}
        render={routeProps => <Redirect {...routeProps} to="/home" />}
      />
      <Route 
        exact
        path="/brochure-site"
        render={routeProps => <Redirect {...routeProps} to="/home" />}
      />
      <Route 
        path={`${prefix}/home`}
        render={routeProps => <Home { ...routeProps} />}
      />
      <Route 
        path={`${prefix}/how-it-works`}
        render={routeProps => <HowItWorks {...routeProps} />}
      />
      <Route 
        path={`${prefix}/contact`}
        render={routeProps => <Contact {...routeProps} />}
      />
      <Route 
        path={`${prefix}/testimonials`}
        render={routeProps => <Testimonials {...routeProps} />}
      />
    </Switch>
  );
};

export default AppRoutes;
